import React, { PureComponent, Fragment } from 'react';
import {
  Popconfirm,
  Row,
  Table,
  Col,
  Checkbox,
  // Select,
  Input,
  Form,
  Button,
  Icon,
  Upload,
  Spin,
  Divider,
  Collapse,
} from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import {
  updateQueryParams,
  // getQueryParams
} from 'utils/urlUtil';
import {
  // tableDataToView,
  SelectAjax,
} from 'components/common';
import {
  BtnSave,
  BtnCancel,
  BtnAddNew,
  BtnSearch,
  BtnClear,
  // BtnExport,
  BtnDelete,
} from 'components/common/button';
import { getKnowledges } from 'services/knowledgeUnit';
import { getListSubject } from 'services/Subject';
import { getListSubjectType } from 'services/subjectType';
import { getCertificateTypes } from 'services/certificateType';
import {
  uploadFilePrograms,
  addProgramDefinition,
  editProgramDefinition,
} from 'services/programDefinition';
import { setFieldsValueCustom } from '../../../utils/formUtil';

const { Panel } = Collapse;
const ADD_NEW_TEXT = 'Thêm Mới';
const EDIT_TEXT = 'Chỉnh Sửa';

@Form.create({ name: 'FormDetail' })
class FormDetail extends PureComponent {
  state = {
    reload: 0,
    data: null,
    tableDataToView: [],
    tableDataToSave: {
      createProgramDetails: [],
      createProgramDetailsObj: {},
    },
    loading: true,
  };

  componentDidMount = () => {
    const { form, data } = this.props;
    if (data) {
      this.setState(
        {
          data: { ...data },
          tableDataToView: [...data.programDetails],
        },
        () => setFieldsValueCustom(form, data)
      );
    }
  };

  componentWillReceiveProps = nextProps => {
    const { data, loading } = this.state;
    const { form } = this.props;
    if (nextProps.data && nextProps.loading === false && (!data || data.id !== nextProps.data.id)) {
      this.setState(
        {
          data: { ...nextProps.data },
          tableDataToView: [...nextProps.data.programDetails],
        },
        () => setFieldsValueCustom(form, nextProps.data)
      );
    }

    if (!nextProps.data && nextProps.loading) {
      this.setState(
        {
          data: null,
          tableDataToView: [],
        },
        () => this.props.form.resetFields()
      );
    }

    if (nextProps.loading !== loading) {
      this.setState({
        loading: nextProps.loading,
      });
    }
  };

  onSubmit = () => {
    const { title, data } = this.props;
    const formFormData = this.getFormatFormData();
    this.setState({
      tableDataToSave: { ...formFormData },
      loading: true,
    });
    this.props.form.validateFields(async err => {
      if (err) {
        return;
      }
      delete formFormData.createProgramDetailsObj;
      delete formFormData.knowledgeId;
      delete formFormData.semesterNo;
      delete formFormData.subjectId;
      try {
        if (title === ADD_NEW_TEXT) {
          await addProgramDefinition(formFormData);
        } else {
          formFormData.id = data.id;
          await editProgramDefinition(formFormData);
        }
        this.props.onSuccess();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  };

  onCancel = () => {
    this.props.onCancel();
  };

  onFilterReady = () => {
    const { title, form, data } = this.props;
    if (title === EDIT_TEXT && data && data.trainTypeId) {
      form.setFieldsValue({
        trainTypeId: data.trainTypeId,
        facultyId: data.facultyId,
        courseId: data.courseId,
        studyFieldId: data.studyFieldId,
      });
    }
  };

  getFormData = () => {
    const tempObj = {
      // createProgramDetails: [],
      createProgramDetailsObj: {},
    };
    this.props.form.validateFields(async (err, values) => {
      Object.keys(values).forEach(value => {
        const keyOfRow = value.split('-')[0];
        const id = value.split('-')[1];
        if (id) {
          if (!tempObj.createProgramDetailsObj[id]) {
            tempObj.createProgramDetailsObj[id] = {};
            tempObj.createProgramDetailsObj[id].id = parseInt(id);
          }
          tempObj.createProgramDetailsObj[id][keyOfRow] = values[value];
        } else {
          tempObj[keyOfRow] = values[value];
        }
      });
      // Object.keys(tempObj.createProgramDetailsObj).forEach((value) => {
      //   tempObj.createProgramDetails.push(tempObj.createProgramDetailsObj[value]);
      // });
    });
    return tempObj;
  };

  getFormatFormData = () => {
    const { tableDataToSave } = this.state;
    const formDataTemp = this.getFormData();
    const assignNewTableDetail = Object.assign(
      {},
      tableDataToSave.createProgramDetailsObj,
      formDataTemp.createProgramDetailsObj
    );
    const assignNewTableData = Object.assign({}, tableDataToSave, formDataTemp);
    assignNewTableData.createProgramDetailsObj = { ...assignNewTableDetail };
    assignNewTableData.createProgramDetails = [
      ...this.convertTableDataObjToArray(assignNewTableDetail),
    ];
    return assignNewTableData;
  };

  handleDelete = async id => {
    const { tableDataToView } = this.state;
    try {
      const temptableDataToView = tableDataToView.filter(element => {
        return element.id !== id;
      });
      this.setState(
        {
          tableDataToView: [...temptableDataToView],
        },
        () => {
          const formFormData = this.getFormatFormData();
          this.setState({
            tableDataToSave: { ...formFormData },
          });
        }
      );
    } catch (err) {
      console.log(err);
    }
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        updateQueryParams(values);
      }
    });
  };

  handleUploadFile = async dataFile => {
    try {
      const res = await uploadFilePrograms(dataFile.file);
      this.setState({
        tableDataToView: [...res.data.programDetails],
      });
    } catch (error) {
      console.log(error);
    }
  };

  handleFilterProgramDetail = () => {
    const { knowledgeId, semesterNo, subjectId } = this.props.form.getFieldsValue([
      'knowledgeId',
      'semesterNo',
      'subjectId',
    ]);
    const formFormData = this.getFormatFormData();
    let filterData = [...formFormData.createProgramDetails];
    if (knowledgeId) {
      filterData = filterData.filter(element => {
        return element.knowledgeId === knowledgeId;
      });
    }

    if (semesterNo) {
      filterData = filterData.filter(element => {
        return element.semesterNo === parseInt(semesterNo);
      });
    }

    if (subjectId) {
      filterData = filterData.filter(element => {
        return element.subjectId === subjectId;
      });
    }

    this.setState({
      tableDataToView: [...filterData],
      tableDataToSave: { ...formFormData },
    });
  };

  handleResetFilterProgramDetail = () => {
    this.props.form.setFieldsValue({
      knowledgeId: null,
      semesterNo: null,
      subjectId: null,
    });
    const formFormData = this.getFormatFormData();
    const filterData = [...formFormData.createProgramDetails];
    this.setState({
      tableDataToView: [...filterData],
      tableDataToSave: { ...formFormData },
    });
  };

  addNew = () => {
    const temptableDataToView = [...this.state.tableDataToView];
    const tempNewData = { ...this.newtableDataToView };
    if (temptableDataToView.length) {
      tempNewData.id = temptableDataToView[temptableDataToView.length - 1].id + 1;
    }
    temptableDataToView.push({ ...tempNewData });
    this.setState({
      tableDataToView: temptableDataToView,
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  convertTableDataObjToArray = obj => {
    const newArray = [];
    Object.keys(obj).forEach(value => {
      newArray.push(obj[value]);
    });
    return newArray;
  };

  newtableDataToView = {
    id: 0,
    programId: 0,
    subjectId: '1',
    subjectLabel: 'test',
    knowledgeId: 1,
    knowledgeLabel: 'Khối kiến thức chung',
    subjectGroupId: 'CB',
    subjectGroupLabel: 'Cơ bản',
    subjectTypeId: 2,
    subjectTypeLabel: 'string123',
    semesterNo: 1,
    noOfCredit: 2,
    multiplyBy: 1,
    theoryPeriod: 45,
    practicePeriod: 0,
    exercisePeriod: 0,
    groupPeriod: 0,
    selfPeriod: 0,
    totalPeriod: 45,
    isSelfChoose: false,
    selfChosenGroup: 0,
    certificateTypeId: 'GDTC',
    notIncludedInGrade: true,
    requiredCredit: 0,
    isForProject: false,
    isForGraduate: false,
    isGraduatedCondition: true,
  };

  columnsMon = [
    {
      title: 'Khối kiến thức',
      dataIndex: 'knowledgeId',
      key: 'knowledgeId',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`knowledgeId-${record.id}`, { initialValue: record.knowledgeId })(
              <SelectAjax service={getKnowledges} />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Học phần',
      dataIndex: 'subjectId',
      key: 'subjectId',
      render: (_text, record) => {
        const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
        const knowledgeId = getFieldValue(`knowledgeId-${record.id}`);
        return (
          <Form.Item>
            {getFieldDecorator(`subjectId-${record.id}`, { initialValue: record.subjectId })(
              <SelectAjax
                setFieldsValue={setFieldsValue}
                service={getListSubject}
                params={{ knowledgeId }}
                allowGetData={!!knowledgeId}
              />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Loại môn học',
      dataIndex: 'subjectTypeId',
      key: 'subjectTypeId',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`subjectTypeId-${record.id}`, {
              initialValue: record.subjectTypeId,
            })(<SelectAjax service={getListSubjectType} />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Kỳ thứ',
      dataIndex: 'semesterNo',
      key: 'semesterNo',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`semesterNo-${record.id}`, { initialValue: record.semesterNo })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`noOfCredit-${record.id}`, { initialValue: record.noOfCredit })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Hệ số',
      dataIndex: 'multiplyBy',
      key: 'multiplyBy',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`multiplyBy-${record.id}`, { initialValue: record.multiplyBy })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Lý thuyết',
      dataIndex: 'theoryPeriod',
      key: 'theoryPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`theoryPeriod-${record.id}`, { initialValue: record.theoryPeriod })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thực hành',
      dataIndex: 'practicePeriod',
      key: 'practicePeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`practicePeriod-${record.id}`, {
              initialValue: record.practicePeriod,
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Bài tập',
      dataIndex: 'exercisePeriod',
      key: 'exercisePeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`exercisePeriod-${record.id}`, {
              initialValue: record.exercisePeriod,
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thảo luận',
      dataIndex: 'groupPeriod',
      key: 'groupPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`groupPeriod-${record.id}`, { initialValue: record.groupPeriod })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tự học',
      dataIndex: 'selfPeriod',
      key: 'selfPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`selfPeriod-${record.id}`, { initialValue: record.selfPeriod })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tổng số tiết',
      dataIndex: 'totalPeriod',
      key: 'totalPeriod',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`totalPeriod-${record.id}`, { initialValue: record.totalPeriod })(
              <Input />
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Nhóm tự chọn',
      dataIndex: 'selfChosenGroup',
      key: 'selfChosenGroup',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`selfChosenGroup-${record.id}`, {
              initialValue: record.selfChosenGroup,
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Tự chọn',
      dataIndex: 'isSelfChoose',
      key: 'isSelfChoose',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isSelfChoose-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isSelfChoose,
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Chứng chỉ',
      dataIndex: 'certificateTypeId',
      key: 'certificateTypeId',
      render: (_text, record) => {
        const { getFieldDecorator, setFieldsValue } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`certificateTypeId-${record.id}`, {
              initialValue: record.certificateTypeId,
            })(<SelectAjax setFieldsValue={setFieldsValue} service={getCertificateTypes} />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Không tính TBCHT',
      dataIndex: 'notIncludedInGrade',
      key: 'notIncludedInGrade',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`notIncludedInGrade-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.notIncludedInGrade,
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Số tín chỉ tiên quyết',
      dataIndex: 'requiredCredit',
      key: 'requiredCredit',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`requiredCredit-${record.id}`, {
              initialValue: record.requiredCredit,
            })(<Input />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn ĐKKL',
      dataIndex: 'isForProject',
      key: 'isForProject',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isForProject-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isForProject,
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn TN',
      dataIndex: 'isForGraduate',
      key: 'isForGraduate',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isForGraduate-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isForGraduate,
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Môn ĐKTN',
      dataIndex: 'isGraduatedCondition',
      key: 'isGraduatedCondition',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`isGraduatedCondition-${record.id}`, {
              valuePropName: 'checked',
              initialValue: record.isGraduatedCondition,
            })(<Checkbox />)}
          </Form.Item>
        );
      },
    },
    {
      title: 'Thao tác',
      key: 'dcn',
      render: (_text, record) => (
        <Fragment>
          <Form.Item>
            <Popconfirm
              title="Bạn có muốn xóa?"
              okText="Xóa"
              cancelText="Hủy"
              onConfirm={() => this.handleDelete(record.id)}
            >
              <BtnDelete />
            </Popconfirm>
          </Form.Item>
        </Fragment>
      ),
    },
  ];

  render() {
    const { title } = this.props;
    const { tableDataToView, loading } = this.state;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const knowledgeId = getFieldValue('knowledgeId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
    };
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Divider className="title" orientation="center">
            {title}
          </Divider>
          <Form layout="vertical" className="form-inline-label">
            <Collapse defaultActiveKey={['1']}>
              <Panel header="Thông tin chung" key="1">
                <Row gutter={24}>
                  <Col span={5}>
                    <Form.Item label="Chương trình đạo tạo">
                      {getFieldDecorator('programNo')(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="SL tín chỉ">
                      {getFieldDecorator('noOfCredit')(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="SL học kỳ">
                      {getFieldDecorator('noOfSemester')(<Input />)}
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <FilterPathEducate
                    classes
                    form={this.props.form}
                    optionFieldDecorator={optionFieldDecorator}
                    onReadyState={this.onFilterReady}
                  />
                </Row>
              </Panel>
              <Panel header="Bộ lọc dữ liệu chi tiết" key="2">
                <Row gutter={24}>
                  <Col span={5}>
                    <Form.Item label="Khối kiến thức">
                      {getFieldDecorator('knowledgeId')(<SelectAjax service={getKnowledges} />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="Kỳ thứ">
                      {getFieldDecorator('semesterNo')(<Input />)}
                    </Form.Item>
                  </Col>
                  <Col span={5}>
                    <Form.Item label="Học phần">
                      {getFieldDecorator('subjectId')(
                        <SelectAjax
                          setFieldsValue={setFieldsValue}
                          service={getListSubject}
                          params={{ knowledgeId }}
                          allowGetData={!!knowledgeId}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={9} style={{ textAlign: 'left' }}>
                    <Form.Item label="actions" className="none-label">
                      {getFieldDecorator('noOfSemester')(
                        <div className="btn-group">
                          <BtnSearch onClick={this.handleFilterProgramDetail} />
                          <BtnClear onClick={this.handleResetFilterProgramDetail} />
                        </div>
                      )}
                    </Form.Item>
                  </Col>
                </Row>
              </Panel>
              <Panel header="Dữ liệu chi tiết" key="3">
                <Row gutter={24}>
                  <Col span={6} style={{ textAlign: 'left' }}>
                    <div className="btn-group">
                      <BtnAddNew onClick={this.addNew} />
                      <Upload
                        accept=".xlsx"
                        beforeUpload={() => false}
                        listType="text"
                        onChange={uploadData => this.handleUploadFile(uploadData)}
                        showUploadList={false}
                      >
                        <Button>
                          <Icon type="upload" /> Chọn file
                        </Button>
                      </Upload>
                    </div>
                  </Col>
                </Row>
                <Row gutter={24}>
                  <Col span={24}>
                    <Table
                      reload={this.state.reload}
                      columns={this.columnsMon}
                      dataSource={tableDataToView}
                      scroll={{ x: true }}
                      rowKey="id"
                    />
                  </Col>
                </Row>
                <Row gutter={24}>
                  <Col offset={10} span={14}>
                    <BtnSave onClick={this.onSubmit} />
                    <BtnCancel onClick={this.onCancel} />
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Form>
        </Spin>
      </Fragment>
    );
  }
}

export default FormDetail;



